import React, { Component } from 'react';
import {Collapse , Jumbotron, Nav,Navbar, NavbarBrand, NavItem,  NavbarToggler, Button, Modal, ModalHeader, ModalBody, Form, Input, Label, FormGroup } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {
    constructor (props) {
        super (props);
        this.state = {
            isNavOpen: false,
            isModalOpen:false,
        }

        this.togglNav = this.togglNav.bind(this);
        this.togglModal = this.togglModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }

    togglNav () {
        this.setState({
            isNavOpen: !this.state.isNavOpen,
        });
    }

    togglModal () {
        this.setState({
            isModalOpen:!this.state.isModalOpen
        });
    }

    handleLogin (event) {
        this.togglModal();
        alert("username: " + this.username.value + " password: " + this.password.value + " remember: " + this.remember.checked
        +' ' + document.getElementById('a').textContent);
        
        event.preventDefault();
    }

    render () {
        return (
            <>
                <Navbar dark expand="md" >
                    <div className="container">

                        <NavbarToggler onClick={this.togglNav} />

                        <NavbarBrand className="mr-auto" href="/">
                             <img src="assets/images/logo.png"  alt="Ristorante Con Fusion" width="41" height="30" />
                         </NavbarBrand>

                         <Collapse isOpen={this.state.isNavOpen} navbar>

                            <Nav navbar>

                                <NavItem>
                                    <NavLink className="nav-link" to="/home">
                                        <span className="fa fa-home fa-lg"></span> Home
                                    </NavLink>
                                </NavItem>

                                <NavItem>
                                    <NavLink className="nav-link" to="/aboutus">
                                        <span className="fa fa-info fa-lg"></span> About Us
                                    </NavLink>
                                </NavItem>

                                <NavItem>
                                    <NavLink className="nav-link" to="/menu">
                                        <span className="fa fa-list fa-lg"></span> Menu
                                    </NavLink>
                                </NavItem>

                                <NavItem>
                                    <NavLink className="nav-link" to="/contactus">
                                        <span className="fa fa-address-card fa-lg"></span> Contact Us
                                    </NavLink>
                                </NavItem>

                            </Nav>

                            <Nav className="ml-auto">
                                <NavItem>
                                    <Button outline onClick={this.togglModal}>
                                        <span className="fa fa-sign-in lg">{ ' '}Login</span>
                                    </Button>
                                </NavItem>
                            </Nav>

                         </Collapse>

                    </div>
                </Navbar>

                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Ristorante con Fusion</h1>
                                <p>We take inspiration from the Worlds best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
                <Modal isOpen={this.state.isModalOpen} toggle={this.togglModal}>
                    <ModalHeader id="a">Login</ModalHeader>
                    <ModalBody toggle={this.togglModal}>
                        <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username" 
                                  innerRef={(input)=> this.username = input}  
                                />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                innerRef={(input) => this.password = input} />
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                    innerRef={(input) => this.remember = input} /> { ' '}
                                    Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" className="bg-primary" >Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>

            </>
        );
    }
}

export default Header;