import React, { Component } from 'react';
import { 
    Card, CardImg,Col, CardTitle, CardImgOverlay , CardBody, CardText, Breadcrumb, BreadcrumbItem, Button,
     Row, Label, Modal, ModalBody, ModalHeader
} from 'reactstrap';
import Moment from 'moment';
import { Link } from 'react-router-dom';
import  { LocalForm, Errors, Control } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';
import { baseUrl } from './../shared/baseUrl';


const RenderDish = ({dish}) => {    
    return (
        <FadeTransform in transformProps={{
            exitTransformProps: 'scale(-50%) translate(-50%)'
        }}>
          <Card>
              <CardImg top src={baseUrl + dish.image} alt={dish.name} />
              <CardImgOverlay className="ml-5">
                  <CardTitle> {dish.name} </CardTitle>
              </CardImgOverlay>
              <CardBody>
                  <CardTitle> {dish.name} </CardTitle>
                  <CardText> {dish.description} </CardText> 
              </CardBody>
          </Card>
        </FadeTransform>
    ); 
}


 

const required = val => val && val.length;
const maxLength = len => val => !val || val.length <= len;
const minLength = len => val => val && val.length >= len;

class CommentForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleSubmit(values) {
    console.log("Current State is: " + JSON.stringify(values));
    //alert("Current State is: " + JSON.stringify(values));
    this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
  }

  render() {
    return (
      <div>
        <Button outline onClick={this.toggleModal}>
          <span className="fa fa-pencil" /> Submit Comment
        </Button>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
          <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
          <ModalBody>
            <LocalForm onSubmit={this.handleSubmit}>
              <Row className="form-group">
                <Label htmlFor="rating" md={12}>
                  Rating
                </Label>
                <Col md={{ size: 12 }}>
                  <Control.select
                    model=".rating"
                    name="rating"
                    className="form-control"
                  >
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                  </Control.select>
                </Col>
              </Row>
              <Row className="form-group">
                <Label htmlFor="author" md={12}>
                  Your Name
                </Label>
                <Col md={12}>
                  <Control.text
                    model=".author"
                    id="author"
                    name="author"
                    placeholder="Your Name"
                    className="form-control"
                    validators={{
                      required,
                      minLength: minLength(3),
                      maxLength: maxLength(15)
                    }}
                  />
                  <Errors
                    className="text-danger"
                    model=".author"
                    show="touched"
                    messages={{
                      required: "Required",
                      minLength: "Must be greater than 2 characters",
                      maxLength: "Must be 15 characters or less"
                    }}
                  />
                </Col>
              </Row>
              <Row className="form-group">
                <Label htmlFor="comment" md={12}>
                  Comment
                </Label>
                <Col md={12}>
                  <Control.textarea
                    model=".comment"
                    id="comment"
                    name="comment"
                    rows={5}
                    className="form-control"
                  />
                </Col>
              </Row>
              <Button type="submit" value="submit" color="primary">
                Submit
              </Button>
            </LocalForm>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

function RenderComments({comments,commentsErrMess, postComment, dishId}){    
    return (
        
      <div>
        <ul className="list-unstyled">
          <Stagger in>
              {comments.map(function(object, i){
                return (
                  <Fade in>
                    <li obj={object.comment} key={i} >
                      <p>{object.comment}</p>
                      <p>--{object.author}, {Moment(object.date).format('MMM d, YYYY')} </p>
                    </li>
                  </Fade>
                );    
            })}
          </Stagger>  
        </ul>
        <CommentForm dishId={dishId} postComment={postComment} />
      </div>
       
    ); 
}




class DishDetail extends Component{

   render () {
    if (this.props.isLoading) {
        return(
            <div className="container">
                <div className="row">            
                    <Loading />
                </div>
            </div>
        );
      }
      else if (this.props.errMess) {
          return(
            <div className="container">
                <div className="row">            
                    <h4>{this.props.errMess}</h4>
                </div>
            </div>
          );
      }
      else if(this.props.dish != null){
          return (
            <div className="container">
                <div clasName="row">
                    <Breadcrumb>
                        <BreadcrumbItem>
                            <Link to="/menu">Menu</Link>
                        </BreadcrumbItem>
                        <Breadcrumb active> {this.props.dish.name} </Breadcrumb>
                    </Breadcrumb>
                    <div className="col-12" >
                        <h3> {this.props.dish.name} </h3>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish = {this.props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m-1">
                        <h2>Comments</h2>
                        <RenderComments comments={this.props.comments}
                            commentsErrMess = {this.props.errMess}
                            postComment={this.props.postComment}
                            dishId={this.props.dish.id}
                        />
                    </div>
                </div>
            </div>);
        } else {
          return(<div></div>);
        }
    }
}

export default DishDetail;