import * as ActionTypes from './ActionTypes';
import { baseUrl } from './../shared/baseUrl';





export const addComment = (comment) => ({
    type: ActionTypes.ADD_COMMENT,
    payload:comment
});

export const postComment = (dishId, rating, author, comment) => (dispatch) => {
    var newComment = {
        dishId: dishId,
        rating: rating,
        author: author,
        comment: comment
    };
    newComment.date = new Date().toISOString();

    fetch(baseUrl + 'comments', {
        method: 'POST',
        body: JSON.stringify(newComment),
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'same-origin'
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ' : ' + response.statusText);
            error.response = response;
            throw error;
        }
    }, error => {
        var errmess = new Error(error.message);
        throw errmess;
    })
    .then(response => response.json())
    .then(response => dispatch(addComment(response)))
    .catch(error => {
        console.error('Post Comments ', error.message);
        alert('Your comment could not be posted\nError: ' + error.message);
    })
} 



//feedbacks
export const addFeedback = (feedback) => ({
    type: ActionTypes.ADD_FEEDBACK,
    payload:feedback
});

export const postFeedback = (firstname, lastname, telnum, email, agree, contactType, message) => (dispatch) => {
    var newFeedback = {
        firstname: firstname,
        lastname: lastname,
        telnum: telnum,
        email: email,
        agree: agree,
        contactType: contactType,
        message:message 
    };
    newFeedback.date = new Date().toISOString();

    fetch(baseUrl + 'feedback', {
        method: 'POST',
        body: JSON.stringify(newFeedback),
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'same-origin'
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ' : ' + response.statusText);
            error.response = response;
            throw error;
        }
    }, error => {
        var errmess = new Error(error.message);
        throw errmess;
    })
    .then(response => response.json())
    .then(response => dispatch(addFeedback(response)))
    .then(response =>
        alert("Thank you for your feedback!\n"+ JSON.stringify(response))
    )
    .catch(error => {
        console.error('Post feedbacks ', error.message);
        alert('Your feedback could not be posted\nError: ' + error.message);
    })
} 

export const fetchFeedbacks = () => (dispatch) => {

    dispatch(feedbacksLoading(true));

    return fetch(baseUrl + 'feedback')
    .then(response => {
        if(response.ok){
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
    }, error => {
        var errmess = new Error(error.message);
        throw errmess;
    })
    .then(response => response.json())  
    .then(feedbacks => dispatch(addFeedbacks(feedbacks)))
    .catch(error => dispatch(feedbacksFailed(error.message)));
}

